<template>
  <div class="my">
    <!-- 头部 -->
    <div class="my-header">
      <p class="my-head-img">
        <img src="../../common/images/my-tou.jpg" alt>
      </p>
      <p class="tel">130****1152</p>
    </div>
    <!-- 导航 -->
    <div class="my-nav">
      <div class="my-nav-item">
        <p class="top-content">
          <span>0</span>张
        </p>
        <p class="bottom-content">
          <span>优惠券</span>
        </p>
      </div>
      <div class="my-nav-item">
        <p class="top-content">
          <span>0</span>元
        </p>
        <p class="bottom-content">
          <span>余额</span>
        </p>
      </div>
    </div>
    <!-- 我的养护订单 -->
    <div class="maintenance-order">
      <div class="top-content">
        <p class="title">我的养护订单</p>
        <p class="all-order">查看所有订单</p>
      </div>
      <ul class="order-nav">
        <li class="order-nav-item">
          <p class="name">待取货</p>
        </li>
        <li class="order-nav-item">
          <p class="name">方案中</p>
        </li>
        <li class="order-nav-item">
          <p class="name">养护中</p>
        </li>
        <li class="order-nav-item">
          <p class="name">待收货</p>
        </li>
        <li class="order-nav-item">
          <p class="name">待评价</p>
        </li>
      </ul>
    </div>

    <!-- 估值寄卖订单 -->
    <div class="valuation-consignment-order">
      <span>估值寄卖订单</span>
      <span>></span>
    </div>

    <!-- 我的信息 -->
    <ul class="my-info-list">
      <li class="my-info-item">
        <p>
          <span>我的地址</span>
        </p>
        <span>></span>
      </li>
      <li class="my-info-item">
        <p>
          <span>联系客服:</span>
          <span>400-601-3233</span>
          <span>(9:00-21:00)</span>
        </p>
        <span>></span>
      </li>
      <li class="my-info-item">
        <p>
          <span>关于包大师</span>
        </p>
        <span>></span>
      </li>
    </ul>

    <!-- 退出登录 -->
    <div class="login-out">
        <span>退出登录</span>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {}
};
</script>

<style lang="less" scoped>
.my {
  //头部
  font-size: 28px;
  .my-header {
    height: 274px;
    background: url("../../common/images/mybg.jpg") 0 0 no-repeat;
    background-size: 100%;
    overflow: hidden;
    .my-head-img {
      width: 150px;
      height: 150px;
      border-radius: 50%;
      margin: 0 auto;
      overflow: hidden;
      margin-top: 30px;
      margin-bottom: 20px;
      img {
        width: 100%;
      }
    }
    .tel {
      padding: 20px;
      text-align: center;
      font-size: 28px;
      color: #fff;
    }
  }
  //导航
  .my-nav {
    width: 100%;
    display: flex;
    align-items: center;
    text-align: center;
    font-size: 28px;
    height: 120px;
    background: #fff;
    overflow: hidden;
    .my-nav-item {
      width: 50%;
      padding: 20px 0;
      .top-content {
        margin-bottom: 20px;
        color: #e6706e;
      }
      .bottom-content {
      }
    }
  }

  //我的养护订单
  .maintenance-order {
    border-top: 20px solid #f5f5f5;
    .top-content {
      padding: 30px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      background: #fff;
      font-size: 28px;
      .title {
      }
      .all-order {
        color: #bbb;
      }
    }

    //我的养护订单导航
    .order-nav {
      padding: 0 30px;
      background: #fff;
      display: flex;
      align-items: center;
      text-align: center;
      border-top: 1px solid #f5f5f5; /*no*/
      .order-nav-item {
        flex: 1;
        padding: 30px 0;
      }
    }
  }

  //估值寄卖订单
  .valuation-consignment-order {
    display: flex;
    align-items: center;
    padding: 30px;
    justify-content: space-between;
    background: #fff;
    font-size: 28px;
    border-top: 20px solid #f5f5f5;
  }

  //我的信息
  .my-info-list {
    background: #fff;
    .my-info-item {
      border-bottom: 1px solid #f5f5f5; /*no*/
      padding: 30px;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }

  //退出登录
  .login-out{
     
      padding: 30px;
      text-align: center;
      margin-top: 20px;
      color: #e6706e;
      font-size: 28px;
      background: #fff;
      margin-bottom: 100px;
  }
}
</style>




